import React, { useState, useEffect } from 'react';
import axios from 'axios';



const API_KEY = '2d602983dec644cdb9602da506617080'; // Reemplaza con tu clave de API de News API

const NoticiasBitcoin = () => {
  const [noticias, setNoticias] = useState([]);

  useEffect(() => {
    async function fetchNoticias() {
      try {
        const response = await axios.get(
          `https://newsapi.org/v2/everything?q=bitcoin+argentina&language=es&apiKey=${API_KEY}`
        );

        setNoticias(response.data.articles);
      } catch (error) {
        console.error('Error al obtener las noticias', error);
      }
    }

    fetchNoticias();
  }, []);

  return (
    <div className='App'>
      <h1>Noticias Financieras en Argentina</h1>
      <ul>
        {noticias.map((noticia, index) => (
          <li key={index}>
            <a href={noticia.url} target="_blank" rel="noopener noreferrer">
              {noticia.title}
            </a>
          </li>
        ))}
      </ul>
    </div>
  );
};

export default NoticiasBitcoin;
