
import React from "react";
import "../views/Home/Home.module.css";
import github from "../assets/github.png";
import linkeding from "../assets/linkeding.png";
import "bootstrap/dist/css/bootstrap.css";


const Footer = () => <footer className="page-footer font-small blue pt-4">
    <div className="container-fluid text-center text-md-left">
        <div className="row">
            <div className="col-md-6 mt-md-0 mt-3">
                <h5 className="text-uppercase">Bigbang Banco</h5>
                <p>Un banco creado con componentes innovadores que destaca
                    la apertura de la Cuenta Bancaria con todas sus funcionalidades y llamadas a Apis 
                    que obtienen informacin de diferntes Apis entre ellas: la Temperatura
                    y noticias financieras actuales.
                    <br/>
                    
                </p>
            </div>

            <hr className="clearfix w-100 d-md-none pb-0"/>

            <div className="col-md-6 mb-md-0 mb-3">
                <h5 className="text-uppercase">Contactos</h5>
                <ul className="list-unstyled">
                    <li><a href="https://github.com/Nathy25"><img src={github} className="mx-2" height='40' alt="imagen" /></a></li>
                    <li><a href="https://www.linkedin.com/in/natalia-durante/"><img src={linkeding} className="mx-2" height='50' alt="imagen" /></a></li>
                   
                </ul>
            </div>
        </div>
    </div>

    <div className="footer-copyright text-center py-1"> 2023 Copyright:
        <p > Natalia Durante</p>
    </div>

</footer>

export default Footer;