import React, { useState, useEffect } from "react";
import axios from "axios";

const WeatherComponent = () => {
  const [weatherData, setWeatherData] = useState(null);

  useEffect(() => {
    const apiKey = "d32ebadbf5b75f6b655622c9076c3f79"; // Reemplaza "TU_API_KEY" con tu clave de API de OpenWeatherMap
    const city = "Buenos Aires,ar"; // Ciudad de Buenos Aires, Argentina
    const apiUrl = `https://api.openweathermap.org/data/2.5/weather?q=${city}&lang=es&units=metric&appid=${apiKey}`;

    axios.get(apiUrl)
      .then((response) => {
        const { main, weather } = response.data;
        const temperature = main.temp;
        const description = weather[0].description;
        const iconCode = weather[0].icon;

        setWeatherData({
          temperature,
          description,
          iconCode,
        });
      })
      .catch((error) => {
        console.error("Error al obtener datos del clima: " + error);
      });
  }, []);

  return (
    <div>
      {weatherData && (
        <div>
          <h2>El tiempo en Buenos Aires, Argentina</h2>
          <p>Temperatura: {weatherData.temperature}C</p>
          <p>Descripcin del clima: {weatherData.description}</p>
          <img
            src={`http://openweathermap.org/img/w/${weatherData.iconCode}.png`}
            alt={weatherData.description}
          />
        </div>
      )}
    </div>
  );
};

export default WeatherComponent;



