import {
    createBrowserRouter,
    RouterProvider,
} from "react-router-dom";



import Home from '../views/Home';

import Banking from "../components/Banking";

const Router = createBrowserRouter([
    {
        path: '/',
        element: <Home/>,
        
    },
    {
        path: '/banking',
        element: <Banking/>,
        
    }
]);



const MyRoutes = () => {
    return (
        <RouterProvider router={Router}/>
    );
};

export default MyRoutes;