
import React, { useState } from 'react';
import { Link } from 'react-router-dom';

import logo from '../assets/logo.svg';
import letra from '../assets/bang2.png';
import NoticiasBancarias from './NoticiasBancarias';
import Footer from './Footer';


function generateRandomAccountNumber() {
  return Math.floor(Math.random() * 1e20).toString().padStart(20, '0');
}

function BankAccountComponent() {
  const [accountHolder, setAccountHolder] = useState('');
  const [accountNumber] = useState(generateRandomAccountNumber());
  const [balance, setBalance] = useState(1000);
  const [transactionAmount, setTransactionAmount] = useState('');

  const deposit = () => {
    const amount = parseFloat(transactionAmount);
    if (!isNaN(amount) && amount > 0) {
      setBalance(balance + amount);
      setTransactionAmount('');
    } else {
      alert('El monto de depsito debe ser un nmero positivo vlido.');
    }
  };

  const withdraw = () => {
    const amount = parseFloat(transactionAmount);
    if (!isNaN(amount) && amount > 0 && balance >= amount) {
      setBalance(balance - amount);
      setTransactionAmount('');
    } else {
      alert('Saldo insuficiente o monto no vlido.');
    }
  };

  return (
    <div className='App'>
      <header className='App-header'>
      <img src={logo} className="App-logo" alt="logo"  />
      <img src={letra} alt="logo" />

      <Link to='/'>Inicio</Link>


      </header>


      <h2>Cuenta Bancaria</h2>
      <p>Nombre del Titular: {accountHolder}</p>
      <p>Nmero de Cuenta: {accountNumber}</p>
      <p>Saldo Actual: ${balance}</p>
      <input
        type="text"
        placeholder="Nombre y Apellido"
        value={accountHolder}
        onChange={(e) => setAccountHolder(e.target.value)}
      />
      <p>Monto a Depositar/Retirar:</p>
      <input
        type="text"
        value={transactionAmount}
        onChange={(e) => setTransactionAmount(e.target.value)}
      />
      <button onClick={deposit}>Depositar</button>
      <button onClick={withdraw}>Retirar</button>

      <div className='col-sm-6'>
        <div className='card'>
          <div className='card-body'>
            <NoticiasBancarias/>
          </div>
        </div>
      </div>


      <div>
        <div className='card'>
          <div className='card-body'>
            <Footer />
          </div>
        </div>
      </div>
    </div>
  );
}

export default BankAccountComponent;